import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { MainLayoutComponent } from './layouts/main-layout/main-layout.component';
import { PageLayoutComponent } from './layouts/page-layout/page-layout.component';

import { HomeComponent } from './pages/home/home.component';
import { ProjectsComponent } from './pages/projects/projects.component';
import { ServicesComponent } from './pages/services/services.component';
import { EventsComponent } from './pages/events/events.component';
import { AboutUsComponent } from './pages/about-us/about-us.component';
import { ContactUsComponent } from './pages/contact-us/contact-us.component';
import { CareerComponent } from './pages/career/career.component';
import { TeamComponent } from './pages/team/team.component';
import { ServicesRoboticsComponent } from './pages/services/robotics/robotics.component';
import { ProjectsLightyComponent } from './pages/projects/lighty/lighty.component';
import { ServicesIotComponent } from './pages/services/iot/iot.component';

const routes: Routes = [
    {
         path: '', pathMatch: 'full', redirectTo: '/'
    }, {
        path: '',
        component: HomeComponent,
        data: { animation: '' }
    }, {
        path: 'home',
        component: HomeComponent,
        data: { animation: 'home' }
    }, {
        path: 'projects',
        component: ProjectsComponent,
        data: { animation: 'projects' }
    },{
        path: 'projects/lighty',
        component: ProjectsLightyComponent,
        data: { animation: 'projects' }
    }, {
        path: 'services',
        component: ServicesComponent,
        data: { animation: 'services' }
    }, {
        path: 'services/robotics',
        component: ServicesRoboticsComponent,
        data: { animation: 'services' }
    }, {
        path: 'services/iot',
        component: ServicesIotComponent,
        data: { animation: 'services' }
    }, {
        path: 'events',
        component: EventsComponent,
        data: { animation: 'paeventsge' }
    },
    {
        path: 'about-us',
        component: AboutUsComponent,
        data: { animation: 'about-us' }
    },
    {
        path: 'contact-us',
        component: ContactUsComponent,
        data: { animation: 'contact-us' }
    },
    {
        path: 'team',
        component: TeamComponent,
        data: { animation: 'team' }
    },
    {
        path: 'career',
        component: CareerComponent,
        data: { animation: 'career' }
    }
];

@NgModule({
    imports: [RouterModule.forRoot(routes)],
    exports: [RouterModule]
})
export class AppRoutingModule { }
